<template>
  <div>
    <div class="order_msg_block">
      <div class="msg_money">￥{{curFee}}</div>
      <div class="msg_item">商品名称：{{goodsName}}</div>
      <div class="msg_item">订单编号：{{billCode}}</div>
    </div>

    <div class="pay_methods_block">
      <div class="pay_methods_title">选择支付方式</div>
      <div class="pay_methods_list">
        <div class="pay_methods_items clear">
          <div class="pay_methods_items_block">
            <img src="../../assets/Pay_icon_wechat.png" class="pay_methods_icon"/>
            <span>微信支付</span>
          </div>
          <div class="pay_methods_items_block">
            <img src="../../assets/Pay_icon_select.png" class="pay_methods_select_status"/>
          </div>
        </div>
      </div>
    </div>

    <globalBtn :value="subminBtn.value" :status="subminBtn.status" :btnState="subminBtn.btnState"
               @validateCode="confirmToPay"></globalBtn>

    <!-- <button @click="scan">扫一扫</button>
        <button @click="location">获取位置</button> -->
  </div>
</template>

<script>
import globalBtn from '@/components/globalBtn.vue'
import {apiUrl} from '@/common/js/api'
import {ajaxPost} from '@/common/js/public'
//import wx from 'weixin-js-sdk'
import {Diolog} from 'vant'

/*
	 =============================================
			1.点击确认支付 发	 apiUrl.payDo 接口发起微信支付，让后台调用统一下单接口
			2.后台返回签名等参数，用 chooseWXPay 调起微信支付，让用户输入密码
			3.用户输入正确密码并确认 -> 在 success 里回调后台 apiUrl.getBillWithSignDetByBillCode 确认是否真正支付成功，调3次，每次隔1秒
			4.后台也确认支付成功后，调用 apiUrl.updateBillByBillCode 创建一个预约，成功则跳到预约页面，若创建预约接口失败再调一次，若第二次再失败则直接提示用户失败
			5.用户输入取消支付或支付失败 -> 跳到订单页面

			所有要给后台用的数据，除了userInfo里的数据，其他都从wmodule的vuex里拿 this.$store.state.wmodule
	 =============================================
*/

export default {
  data () {
    return {
      goodsName: '挂号费',
      billCode: this.$store.state.wmodule.curBillCode,
      billId: '', 
      curFee: this.$store.state.wmodule.curFee, //金额
			doctName: this.$store.state.wmodule.curDoctorName, // 医生名称
			deptName: this.$store.state.wmodule.curDeptName, // 科室名称
			hospName: this.$store.state.wmodule.curHospitalName, // 医院名称
			signType: this.$store.state.wmodule.curDoctorLevel, // 医生等级
			appointmentDate: this.$store.state.wmodule.curAppointmentDate, // 预约时间
			appointmentTime: this.$store.state.wmodule.curAppointmentTime, // 预约时间
			hospId: this.$store.state.wmodule.curHospitalId,
			deptId: this.$store.state.wmodule.curDeptId,
			doctId: this.$store.state.wmodule.curDoctorId,
			doctorSignTimeId: this.$store.state.wmodule.curAppointmentNumId, // 选择号源时的id
			doctorScheduleId: this.$store.state.wmodule.curDoctScheduleId, 
      subminBtn: {
        value: '确认支付',
        status: 'normal register_btn',
        btnState: false
      },
      ifPaySuccessCount: 0, // 重新调用查询支付状态接口的次数
      ifRecallAppointment: false // 是否调用过第二次创建预约接口
    }
  },
  components: {
    globalBtn
  },
  created () {
    console.log(this.$store.state.wmodule);
    // 配置wx js-sdk
    /* ajaxPost(apiUrl.getWXConfigParam, {
              'url': window.location.href.split('#')[0]
          }, (data) => {
              data = data.data.body.data;
              wx.config({
                  debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                  appId: data.appId, // 必填，公众号的唯一标识
                  timestamp: data.timestamp, // 必填，生成签名的时间戳
                  nonceStr: data.nonceStr, // 必填，生成签名的随机串
                  signature: data.signature,// 必填，签名
                  jsApiList: ['chooseWXPay', 'scanQRCode', 'getLocation'] // 必填，需要使用的JS接口列表
              });
          }) */
          console.log(this.$store.state.wmodule)
  },
  computed: {
    curAppointmentStartTime(){
      return this.$store.state.wmodule.curAppointmentStartTime
    },
    curAppointmentEndTime(){
      return this.$store.state.wmodule.curAppointmentEndTime
    },
    updataBillCode () {
      return this.$store.state.wmodule.curBillCode
    },
    updataFee () {
      return this.$store.state.wmodule.curFee
    },
		updataDeptName () {
			return this.$store.state.wmodule.curDeptName
		},
		updataDoctName () {
			return this.$store.state.wmodule.curDoctorName
		},
		updataHospName () {
			return this.$store.state.wmodule.curHospitalName
		},
		updataSignType () {
			return this.$store.state.wmodule.curDoctorLevel
		},
		updataHospId () {
			return this.$store.state.wmodule.curHospitalId
		},
		updataDeptId () {
			return this.$store.state.wmodule.curDeptId
		},
		updataDoctId () {
			return this.$store.state.wmodule.curDoctorId
		},
		updataDoctorSignTimeId () {
			return this.$store.state.wmodule.curAppointmentNumId
		},
		updataDoctorScheduleId () {
			return this.$store.state.wmodule.curDoctScheduleId
		},
		updateAppointmentDate() {
			return this.$store.state.wmodule.curAppointmentDate
		},
		updateAppointmentTime() {
			return this.$store.state.wmodule.curAppointmentTime
		},
  },
  watch: {
    updataBillCode: function (newVal, oldVal) {
      this.billCode = newVal
    },
    updataFee: function (newVal, oldVal) {
      this.curFee = newVal
    },
		updataDeptName: function (newVal, oldVal) {
			this.deptName = newVal
		},
		updataDoctName: function (newVal, oldVal) {
			this.doctName = newVal
		},
		updataHospName: function (newVal, oldVal) {
			this.hospName = newVal
		},
		updataSignType: function (newVal, oldVal) {
			this.signType = newVal
		},
		updataHospId: function (newVal, oldVal) {
			this.hospId = newVal
		},
		updataDeptId: function (newVal, oldVal) {
			this.deptId = newVal
		},
		updataDoctId: function (newVal, oldVal) {
			this.doctId = newVal
		},
		updataDoctorSignTimeId: function (newVal, oldVal) {
			this.doctorSignTimeId = newVal
		},
		updataDoctorScheduleId: function (newVal, oldVal) {
			this.doctorScheduleId = newVal
		},
		updateAppointmentDate: function (newVal, oldVal) {
			this.appointmentDate = newVal
		},
		updateAppointmentTime: function (newVal, oldVal) {
			this.appointmentTime = newVal
		},
  },
  methods: {
    wxPayFn (timestamp, nonceStr, prepay_id, paySign) {
      /* var _this = this;
				wx.chooseWXPay({
					timestamp: timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
					nonceStr: nonceStr, // 支付签名随机串，不长于 32 位
					package: 'prepay_id=' + prepay_id, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
					signType: 'MD5', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
					paySign: paySign, // 支付签名
					success: (res) => {
						// 支付成功，回调后台确认是否真正支付成功，调3次，每次隔1秒
						let getIfOrderSuccess = () => {
							ajaxPost(apiUrl.getBillWithSignDetByBillCode, {
								'billCode': _this.billCode
							}, (data) => {
								data = data.data.body
								if (data.resultCode == 1000) {
									_this.billId = data.data.id
									// 支付成功，创建新订单后，跳转成功页面
									console.log(_this.signType);
									let createNewAppoitment = () => {
										ajaxPost(apiUrl.add, {
											'billCode': _this.billCode, // 订单号
											'deptName': _this.deptName, // 科室名称
											'doctName': _this.doctName, // 医生名称
											'signTimeStart':  _this.appointmentDate + ' ' + _this.curAppointmentStartTime + ':00', // 预约时间Start
											'signTimEnd': _this.appointmentDate + ' ' + _this.curAppointmentEndTime + ':00', // 预约时间End
											//'signTime': _this.appointmentDate + ' ' + _this.appointmentTime + ':00', // 预约时间
											'signFee': (_this.curFee * 100) + '', // 金额，分为单位
											'signType': _this.signType, // 医生级别
											'feeType': '1', // 币种
											'userId': JSON.parse(sessionStorage.getItem('userInfo')).userId,
											'hospId': _this.hospId,
											'deptId': _this.deptId,
											'doctId': _this.doctId,
											'remark': '', // 备注
											'createUser': JSON.parse(sessionStorage.getItem('userInfo')).userName, // 用户名
											'updateUser': JSON.parse(sessionStorage.getItem('userInfo')).userName, // 用户名
											'billId': _this.billId,
											'hospName': _this.hospName,
											'doctorSignTimeId': _this.doctorSignTimeId, // 选择号源时的id
											'doctorScheduleId': _this.doctorScheduleId // 选择号源时的ScheduleId
										}, (data) => {
											data = data.data.body
											// 创建预约成功，跳转页面
											this.$router.push('/appointment/paySuccess')
										}, (error) => {
											// 创建预约失败，再调一次，不行就弹框联系客服，然后跳到我的页面
											if (!_this.ifRecallAppointment) {
												_this.ifRecallAppointment = true
												createNewAppoitment();
											} else {
												// 真的创建失败了，提示
												_this.$dialog.alert({
													message: '创建预约失败，请联系客服'
												}).then(() => {
													_this.$router.push('/user/userOptions')
												})
											}
										})
									}
									createNewAppoitment();
								} else {
									// 后台支付未成功，等1秒后继续查询或次数>3显示失败
									if (_this.ifPaySuccessCount < 3) {
										setTimeout(() => {
											_this.ifPaySuccessCount++
											getIfOrderSuccess()
										}, 1000)
									} else {
										// 提示支付失败，跳到订单列表
										_this.$dialog.alert({
											message: '支付失败'
										}).then(() => {
											this.$router.push('/user/myOrder')
										})
									}
								}
							})
						}
					getIfOrderSuccess();
				},
				cencel: function(res){
					//支付取消，跳到订单列表
					this.$router.push('/user/myOrder');
				},
				fail: function(res){
					// 支付失败回调函数，跳到订单列表
					console.warm('支付失败：', res);
					this.$router.push('/user/myOrder');
				}
			}); */

      // test success
      this.$router.push('/appointment/paySuccess')
    },
    confirmToPay () {
      this.subminBtn.btnState = true
      var _this = this
      // 请求后台,下单获取签名
      /* ajaxPost(apiUrl.payDo, {
                  'userId': JSON.parse(sessionStorage.getItem('userInfo')).userId,
                  'total_fee': '1000', //分为单位
                  'billCode': _this.billCode //订单号
              }, (data) => {
                  _this.subminBtn.btnState = false;
                  data = data.data.body;
                  var dataMsg = data.data;
                  _this.wxPayFn(dataMsg.timestamp, dataMsg.nonceStr, dataMsg.prepay_id, dataMsg.paySign);
              }) */

      // test
      _this.$dialog.confirm({
        message: '请输入密码',
        title: '支付订单'
      }).then(() => {
        // 更新订单状态为成功
        ajaxPost(apiUrl.updateBillByBillCode, {
          'status': '1',
          'billCode': _this.billCode
        }, (data) => {
          _this.subminBtn.btnState = false
          data = data.data.body
          if (data.resultCode == 1000) {
            // 支付成功，回调后台确认是否真正支付成功，调3次，每次隔1秒
            let getIfOrderSuccess = () => {
              ajaxPost(apiUrl.getBillWithSignDetByBillCode, {
                'billCode': _this.billCode
              }, (data) => {
                data = data.data.body
                if (data.resultCode == 1000) {
                  _this.billId = data.data.id
                  // 支付成功，创建新订单后，跳转成功页面
                  console.log(_this.signType);
                  let createNewAppoitment = () => {
                    ajaxPost(apiUrl.add, {
                      'billCode': _this.billCode, // 订单号
                      'deptName': _this.deptName, // 科室名称
                      'doctName': _this.doctName, // 医生名称
											'signTimeStart':  _this.appointmentDate + ' ' + _this.curAppointmentStartTime + ':00', // 预约时间Start
											'signTimEnd': _this.appointmentDate + ' ' + _this.curAppointmentEndTime + ':00', // 预约时间End
                      //'signTime': _this.appointmentDate + ' ' + _this.appointmentTime + ':00', // 预约时间
                      'signFee': (_this.curFee * 100) + '', // 金额，分为单位
                      'signType': _this.signType, // 医生级别
                      'feeType': '1', // 币种
                      'userId': JSON.parse(sessionStorage.getItem('userInfo')).userId,
                      'hospId': _this.hospId,
                      'deptId': _this.deptId,
                      'doctId': _this.doctId,
                      'remark': '', // 备注
                      'createUser': JSON.parse(sessionStorage.getItem('userInfo')).userName, // 用户名
                      'updateUser': JSON.parse(sessionStorage.getItem('userInfo')).userName, // 用户名
                      'billId': _this.billId,
                      'hospName': _this.hospName,
                      'doctorSignTimeId': _this.doctorSignTimeId, // 选择号源时的id
                      'doctorScheduleId': _this.doctorScheduleId // 选择号源时的ScheduleId
                    }, (data) => {
                      data = data.data.body
                      // 创建预约成功，跳转页面
                      this.$router.push('/appointment/paySuccess')
                    }, (error) => {
                      // 创建预约失败，再调一次，不行就弹框联系客服，然后跳到我的页面
                      if (!_this.ifRecallAppointment) {
                        _this.ifRecallAppointment = true
                        createNewAppoitment();
                      } else {
                        // 真的创建失败了，提示
                        _this.$dialog.alert({
                          message: '创建预约失败，请联系客服'
                        }).then(() => {
                          _this.$router.push('/user/userOptions')
                        })
                      }
                    })
                  }
                  createNewAppoitment();
                } else {
                  // 后台支付未成功，等1秒后继续查询或次数>3显示失败
                  if (_this.ifPaySuccessCount < 3) {
                    setTimeout(() => {
                      _this.ifPaySuccessCount++
                      getIfOrderSuccess()
                    }, 1000)
                  } else {
                    // 提示支付失败，跳到订单列表
                    _this.$dialog.alert({
                      message: '支付失败'
                    }).then(() => {
                      this.$router.push('/user/myOrder')
                    })
                  }
                }
              })
            }
            getIfOrderSuccess()
          }
        }, () => {
          _this.subminBtn.btnState = false
          this.$notify({
            message: '服务器错误',
            duration: 1000
          }).then(() => {
            this.$router.push('/user/myOrder')
          })
        })
      }).catch(() => {
        // 支付取消，跳到订单列表
        this.$router.push('/user/myOrder')
      })

      /* _this.wxPayFn(1,1,1,1); */
    },
    // wx sdk 测试方法
    scan () {
      wx.scanQRCode({
        needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
        scanType: ['qrCode', 'barCode'], // 可以指定扫二维码还是一维码，默认二者都有
        success: (res) => {
          var result = res.resultStr // 当needResult 为 1 时，扫码返回的结果
        }
      })
    },
    location () {
      wx.getLocation({
        type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
        success: (res) => {
          var latitude = res.latitude // 纬度，浮点数，范围为90 ~ -90
          var longitude = res.longitude // 经度，浮点数，范围为180 ~ -180。
          var speed = res.speed // 速度，以米/每秒计
          var accuracy = res.accuracy // 位置精度
          console.log(latitude, longitude, speed, accuracy)
        }
      })
    }
  }
}
</script>

<style scoped lang="less">
  @import '../../common/css/global_pyt.less';

  .order_msg_block {
    /* background-image: url(../../assets/Pay_price_bg.png); */
    border: 5px solid #ccc;
    border-image: url(../../assets/Pay_price_bg_bottom.png) 5 5 repeat;
    background-size: 100%;
    background-repeat: no-repeat;
    color: #272727;
    padding: .14*2rem;
    box-sizing: border-box;
    margin: .14*2rem;
    margin-top: .58*2rem;
    line-height: .22*2rem;
    box-shadow: 3px 3px 10px 5px rgba(51, 145, 232, 0.10);

    .msg_money {
      font-size: .24*2rem;
      font-weight: bold;
      line-height: .34*2rem;
      margin-bottom: .14*2rem;
    }

    .msg_item {
      opacity: .6;
      font-size: .15*2rem;
    }
  }

  .pay_methods_block {
    color: #272727;
    line-height: .22*2rem;
    margin: .3*2rem;

    .pay_methods_title {
      font-weight: bold;
      margin-bottom: .12*2rem;
    }

    .pay_methods_list {
      border-top: 1px solid #F6F7F8;
      margin-bottom: 1.3*2rem;

      .pay_methods_items {
        border-bottom: 1px solid #F6F7F8;
        padding: .12*2rem .04*2rem;

        .pay_methods_items_block {
          float: left;
          line-height: .2*2rem;

          &:last-child {
            float: right;
          }

          .pay_methods_icon {
            height: .14*2rem;
            margin-right: .05*2rem;
          }

          .pay_methods_select_status {
            height: .16*2rem;
          }
        }
      }
    }
  }
</style>



